@import url(https://fonts.googleapis.com/css2?family=Kaisei+Tokumin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primary-white : #fff;
    --primary-blue : #4C43D4;
    --primary-dark : rgba(255,255,255,0);
    --primary-orange : #f9ab00;
}

body{
    font-family: 'Kaisei Tokumin', serif;
}


.navbar-custom {
    background-color: #191a2b;
    
}

.navbar-light .navbar-toggler {
    color: white;
    border-color: white;
}

.logo{
    height: 2.8rem;
    width: 4.4rem;
}

nav a.nav-link {
    font-weight: 200;
    letter-spacing: 0.0625rem;
    cursor: pointer;
}

nav ul li {
    
    text-transform: uppercase;
    
}
.navbar-nav{
    margin-left: auto;
}

.navbar-nav > li > a {
    margin-left: 0.625rem;
    margin-right: 0.625rem;
    color: azure !important;
    border-bottom: 0.1875rem solid rgba(255,255,255,0);
    border-bottom: 0.1875rem solid var(--primary-dark) ;
}

.navbar-nav > li > a:focus {
    border-bottom: 0.1875rem solid #4C43D4;
    border-bottom: 0.1875rem solid var(--primary-blue);
}

.navbar-nav > li > a:hover {
    border-bottom: 0.1875rem solid #4C43D4;
    border-bottom: 0.1875rem solid var(--primary-blue);
    color: sandybrown !important;
}

/********************Header*************************/

.bg-image {
    
    position: relative;
    background: #1a1c2b url(/portfolio/static/media/background.f3253abd.jpg) no-repeat;
    background-size: contain;
    background-position:center;
    height:90vh;
    overflow: hidden;
    
}
.bg-text  {

  background-color: rgb(0,0,0) ;
  background-color: rgba(0,0,0, 0.65) ; 
  color: #f9ab00; 
  color: var(--primary-orange) ;
  text-transform: uppercase ;
  font-weight: bold ;
  border: 3px solid #f1f1f1 ;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family:'Cormorant Garamond', serif;
  z-index: 2;
  height: 20vh;
  width: 60vw ; 
  padding: 20px 0 20px 0;
  
  text-align: center;
   justify-content: center;
 

    
}
.typed-text {
    text-transform: capitalize;
    color: white;
    letter-spacing: 1.5px;
}



.tsparticles-canvas-el{
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}
nav{
    z-index: 2;
}

/********************Common Update*************************/



.title_heading {
    text-align: center;
    color: rgb(141, 133, 133);
    
}

.about__main {
    background-color: #1a1c2b;
    height: 100vh;
    padding-bottom:30px;
}
h1.aboutme-tite{
    text-align: center;
    color: whitesmoke;
    text-decoration: underline;
}
.pic-container .img-fluid{
    border: #f9ab00 solid 0.5px;
    border-radius: 30px;
    box-shadow: #0011f9 0 25px 80px -15px;
    height: 50%;
    width: auto;
}

.pic-container{
    display: flex;
    justify-content: center;
    padding-top: 40px;
}

.navbar-light .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 165, 0, 0.8%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}
.navbar-light .navbar-toggler {
    border-color:orange;
    border-radius: 2px;
    
}

#About .title_heading {
    margin-bottom: 50px;
}

.about_content {
    padding-top: 50px;
    padding-bottom: 0;
}
.name{
    font-family: 'Times New Roman', Times, serif;
    letter-spacing: 1px;
    
    
}
.CVbtn{
    background-color:#1a1c2b;
    color: white;
    padding: 9px;
    border: white solid 1px;
    border-radius: 15px;
    font-weight: 500;
}
.CVViewbtn{
    background-color: #1a1c2b;
    color: white;
    padding: 9px 30px;
    margin: 0px 20px;
    border: white solid 1px;
    border-radius: 15px;
    font-weight: 500;
}
.CVViewbtn:hover {
    background-color: aliceblue;
    color: #0011f9;
}
.CVbtn:hover {
    background-color: aliceblue;
    color: #0011f9;
}
h3{
    color: whitesmoke;
    text-align: start;
    word-spacing: 3px;
   
}
p{
    color: whitesmoke;
    line-height: 35px;
    font-size: 20px;
}

@media screen and (max-width:576px) {
    .pic-container .img-fluid {
        height: auto;
        width: 70%;
    }
}

@media screen and (min-width: 576px) and (max-width:768px) {
    .pic-container .img-fluid {
        height: auto;
        width: 100%;
    }
}
@media screen and (max-width:768px) {
    
    .about__main {
        height: auto;
    }
    .CVbtn{
        display: none;
    }
    
    .about__heading{
        display: none;
    }
    .pic-container{
        padding-bottom: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    p{
        color: whitesmoke;
        line-height: 30px;
        font-size: 18px;
    }
}




.contact_main {
    background-color: #151729;
    height: 40vh;
}




@media screen and (max-width:768px) {
    .contact_main {
        height: 25vh;
    }
    .contact_main .img-fluid {
        height: auto;
        width: 85%;
    }
    .contact_main .col-sm-3 {
        padding-right: 50px;
    }
    .contact_main .col-3 {
        flex: 0 0 auto;
        width: 30%;
    }
}

@media screen and (min-width:768px) {
    .contact_main .img-fluid {
        height: auto;
        width: 30%;
    }
    
    .contact_main .img-fluid:hover{
        height: auto;
        width: 35%; 
    }
    
    .contact_main .col-3 {
        flex: 0 0 auto;
        width: 27%;
    }
}



.project_main{
    background-color: #1a1c2b;
    height: auto;
}
.project_main .title_heading {
    padding-top: 50px;
    padding-bottom: 50px;
}
.project_img {
    display:flex;
    align-items:center;
    justify-content: center;
}

.img-fluid {
    height: auto;
    width: 95%;
}
.card-section {
    display: flex;
    justify-content: start;
    padding-bottom: 35px;
}

.card:hover{
    border: 1px solid black;
    box-shadow:7px 6px 5px blue ;
}
.card-section:nth-child(3) {
    justify-content: center;
}
.card-section:nth-child(4) {
    justify-content: end;
}
.card-title {
    font-size: 25px;
    text-align: center;
    font-weight: 700;
}
.card-text {
    color: #1a1c2b;
    line-height: normal;
}

#projects .item {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 800px;
    margin: 0 auto;
    margin-bottom: 15rem;
}
#projects .item:nth-child(even){
    flex-direction: row-reverse;
}
#projects .item .left {
    flex: 1 1;
}
#projects .item .right {
    flex: 1 1;
    margin-left: -150px;
}
#projects .item {
    height: 50%;
    width: 50%;
}
.project-desc {
    color: black;
}
#projects .img {
    height: auto;
    width: 50%;
}



@media screen and (max-width: 576px) {
.img-fluid {
    height: auto;
    width: 100%;
}
}
.Education_main{
    background-color: #1a1c2b;
    height:auto;
    padding-bottom:30px;
}

#Education h3 {
    color:whitesmoke;
    font-weight: bold;
}
#Education .title_heading {
    margin-bottom: 50px;
}

.qualification_data {
    display:grid;
    grid-template-columns: 1fr -webkit-max-content 1fr;
    grid-template-columns: 1fr max-content 1fr;
    grid-column-gap: 1.5rem;
    -webkit-column-gap: 1.5rem;
            column-gap: 1.5rem;
}

.qualification_subtitle {
    display:inline-block;
    font-size: 22px;
    font-weight: 400;
    color: white;
}

.qualification_calendar {
    font-size: 20px;
    color: white;
}
.qualification_percentage {
    font-size:20px;
    color: white;
}
.qualification_round {
    display:inline-block;
    width: 13px;
    height:13px;
    background-color:whitesmoke;
    border-radius:50%
}

.qualification_line {
    display:block;
    width:1px;
    height:100%;
    background-color:whitesmoke;
    -webkit-transform:translate(6px, -7px);
            transform:translate(6px, -7px);
}
